<template>
    <div class="display" :style="{color: textColor}">
        <div id="description">
            <div id="greeting">{{message.greeting}}</div>
            <div id="info">{{message.content}}</div>
            <ContactMethods v-show="showContacts"></ContactMethods>
            <Books v-show="showBooks"></Books>
        </div>
        <BlogDisplay id="desc" v-show="!(showContacts || showBooks || github || goBack)"></BlogDisplay>
    </div>
</template>

<script>
    import ContactMethods from '../extras/ContactMethods';
    import Books from '../books/Books';
    import BlogDisplay from './BlogDisplay';

    export default {
        name: "BlogShowcase",
        data: function () {
            return {
                textColor: '#e2e6e8',
                colors: ['#1c1f21', '#e2e6e8'],
                c: 0,
                messages: {
                    original: {
                        greeting: "",
                        content:  ""
                    },
                    github: {
                        greeting: "Goodbye, World!",
                        content: "Thank you for visiting my site and, if you see something on my GitHub that intrigues" +
                        " you, feel free to reach out!"
                    },
                    contact: {
                        greeting: "Methods of Contact",
                        content: ""
                    },
                    books: {
                        greeting: "List of books I have read.",
                        content: "My goal is at least one per week, no matter what."
                    },
                    gohome: {
                        greeting: "Going Back?",
                        content: "Hope you found something useful!"
                    }
                },
                message: {
                    greeting: "",
                    content:  ""
                },
                showContacts: false,
                showBooks: false,
                goBack: false,
                github: false
            }
        },
        components: {
            BlogDisplay,
            ContactMethods,
            Books
        },
        mounted() {
            this.$root.$on('Original', () => {this.message = this.messages.original; this.textColor = this.colors[this.c];
                this.c = (this.c + 1)%2; this.showContacts = false; this.showBooks = false; this.goBack = false;
                this.github = false;});
            this.$root.$on('GitHub', () => {this.message = this.messages.github; this.textColor = this.colors[this.c];
                this.github = true; this.c = (this.c + 1)%2;});
            this.$root.$on('Contact', () => {this.message = this.messages.contact; this.textColor = this.colors[this.c];
                this.c = (this.c + 1)%2; this.showContacts = true;});
            this.$root.$on('cd ../', () => {this.message = this.messages.gohome; this.textColor = this.colors[this.c];
                this.c = (this.c + 1)%2; this.goBack = true;});
            this.$root.$on('Books', () => {this.message = this.messages.books; this.textColor = this.colors[this.c];
                this.c = (this.c + 1)%2; this.showBooks = true;});
        }
    }

</script>

<style scoped>
.display {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-rows: 25% 25% 25% 25%;
    z-index: 2;
    opacity: 50%;
}

#description {
    margin: auto;
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 5;
}

#desc {
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 5;
}

#info {
    position: relative;
    opacity: 0;
    top: 1em;
    animation: animateOverlay 2s ease-in-out forwards;
    animation-delay: 1.5s;
}

#greeting {
    position: relative;
    opacity: 0;
    top: 1em;
    animation: animateOverlay 1s ease-in-out forwards;
}

@keyframes animateOverlay {
    to {
        opacity: 1;
        top: 0;
    }
}

a {
    text-decoration: none;
    color: inherit;
}

#my-portfolio {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 3;
    grid-row-end: 4;
    position: relative;
    opacity: 0;
    top: 1em;
    animation: animateOverlay 1s ease-in-out forwards;
    animation-delay: 3.5s;
}

</style>