<template>
    <div id="blog-container" class="card text-center">
        <div card="card-title">
            <h4 class="mt-5 mb-0">
                <button class="btn btn-outline-dark btn-sm mr-1" @click="firstPost">First</button>
                <button class="btn btn-outline-dark btn-sm" @click="previousPost"> < </button>
                <a :href="projects[ind].url" class="ml-3 mr-3">{{projects[ind].title}}</a>
                <button class="btn btn-dark btn-sm mr-1" @click="nextPost"> > </button>
                <button class="btn btn-dark btn-sm" @click="lastPost">Last</button>
            </h4>
        </div>
        <div class="card-body">
            <Project :body="projects[ind].body"></Project>
        </div>
    </div>
</template>

<script>
    import Project from './Project';
    export default {
        name: "ProjectList",
        components: {
            Project
        },
        data: function () {
            return {
                projects: [
                    {
                        url: "https://budgetbuddy-dcd66.firebaseapp.com",
                        title: "BudgetBuddy",
                        body: `This is a small React webapp I built in order to familiarize myself with React. The app
                        serves as a very simple budgeting application. It shows a basic visualization, using the Google
                        Charts API, of a users monthly expenses and also displays how much money the user would ideally
                        have left over each month based on their income. Users can create a profile, become
                        authenticated, login, logout, and enter their income/expense streams to keep track of their
                        budgeting needs. All info is stored in a database a given user can view only his/her own data.`
                    }
                ],
                ind: 0
            }
        },
        methods: {
            firstPost() {
                this.ind = 0;
            },
            previousPost() {
                if(this.ind !== 0)
                    --this.ind;
                else
                    this.ind = this.projects.length - 1;
            },
            nextPost() {
                if(this.ind === this.projects.length - 1)
                    this.ind = 0;
                else
                    ++this.ind;
            },
            lastPost() {
                this.ind = this.projects.length - 1;
            }
        }
    }
</script>

<style scoped>
    #blog-container {
        margin: auto;
        grid-column-start: 1;
        grid-column-end: 5;
        grid-row-start: 1;
        grid-row-end: 4;
    }
    div {
        color: #1c1f21;
    }
</style>